/* ============================
   CONTENT INTELLIGENCE LAYER
============================ */

const pick = <T,>(arr: T[]) => arr[Math.floor(Math.random() * arr.length)];

const detectIntent = (subject: string) => {
  const s = subject.toLowerCase();
  if (s.includes("follow")) return "followup";
  if (s.includes("proposal") || s.includes("partnership")) return "proposal";
  if (s.includes("update")) return "update";
  if (s.includes("meeting") || s.includes("call")) return "meeting";
  return "general";
};

const generateSubject = (
  base: string,
  mode: string,
  urgency: boolean,
  tone: string
) => {
  const intent = detectIntent(base);
  const core = base || "Business Update";

  const pools: Record<string, string[]> = {
    followup: [
      `Following up on ${core}`,
      `Next steps on ${core}`,
      `Checking back on ${core}`
    ],
    proposal: [
      `Proposal discussion: ${core}`,
      `Exploring ${core} further`,
      `Partnership opportunity ‚Äî ${core}`
    ],
    meeting: [
      `Scheduling time re: ${core}`,
      `Quick sync on ${core}`,
      `Aligning on ${core}`
    ],
    update: [
      `Update on ${core}`,
      `Latest developments ‚Äî ${core}`,
      `Status update: ${core}`
    ],
    general: [
      core,
      `Regarding ${core}`,
      `${core} ‚Äî discussion`
    ]
  };

  let subject = pick(pools[intent]);

  if (urgency) {
    subject =
      tone === "Professional" || tone === "Urgent"
        ? `Time-sensitive: ${subject}`
        : `Quick note: ${subject}`;
  }

  return `Subject: ${subject}`;
};

const opener = (
  recipient: string,
  tone: string,
  warmth: boolean,
  humanize: boolean,
  mode: string
) => {
  const name = recipient || "there";

  const greetings: Record<string, string[]> = {
    Professional: [`Dear ${name},`, `Hello ${name},`],
    Casual: [`Hi ${name},`, `${name} ‚Äî`],
    Urgent: [`${name},`, `Hi ${name},`],
    Empathetic: [`Hi ${name},`, `Dear ${name},`]
  };

  let line = pick(greetings[tone] || greetings.Professional);

  if (mode !== "Shorten" && (warmth || humanize)) {
    line += `\n\nI hope you're doing well.`;
  }

  return line;
};

const body = (
  points: string,
  mode: string,
  options: EmailState["options"]
) => {
  const p = points || "the points we discussed";

  const strategies: Record<string, string[]> = {
    Shorten: [
      `Checking in regarding ${p}.`,
      `Quick follow-up on ${p}.`,
      `Revisiting ${p}.`
    ],
    Expand: [
      `I wanted to provide additional context around ${p}.\n\nGiven the importance of this, I believe alignment here will help us move forward confidently.`,
      `To expand on ${p}, I‚Äôve outlined the key considerations below so we can ensure clarity.`,
      `There‚Äôs a bit more nuance around ${p} that‚Äôs worth addressing before next steps.`
    ],
    Formal: [
      `I am writing to formally address ${p}.\n\nPlease review the details at your convenience.`,
      `This message serves to follow up on ${p}.`,
      `Per our prior discussion, I am seeking an update regarding ${p}.`
    ],
    Punchy: [
      `Bottom line on ${p}: we‚Äôre ready to move.`,
      `Here‚Äôs the situation with ${p}.`,
      `Quick status on ${p}.`
    ],
    Original: [
      `I wanted to touch base regarding ${p}.`,
      `Following up on ${p}.`,
      `Reaching out about ${p}.`
    ]
  };

  let text = pick(strategies[mode] || strategies.Original);

  if (options.simplify) {
    text = text.replace(/regarding|regards|per our/g, "about");
  }

  if (options.formal) {
    text = text.replace("I'm", "I am").replace("we‚Äôre", "we are");
  }

  return text;
};

const closing = (
  mode: string,
  tone: string,
  options: EmailState["options"]
) => {
  let cta = "";

  if (options.includeCTA) {
    const soft = ["Let me know your thoughts.", "Happy to discuss further."];
    const medium = ["When would be a good time to connect?", "Shall we schedule a quick call?"];
    const strong = ["Please confirm next steps.", "I‚Äôd appreciate a response today."];

    cta = options.addUrgency
      ? pick(strong)
      : options.addWarmth
      ? pick(soft)
      : pick(medium);
  }

  const signoffs: Record<string, string[]> = {
    Professional: ["Best regards,", "Sincerely,"],
    Casual: ["Best,", "Cheers,"],
    Urgent: ["Thanks,", "Best,"],
    Empathetic: ["Kind regards,", "Warmly,"]
  };

  const signoff =
    mode === "Formal"
      ? pick(signoffs.Professional)
      : pick(signoffs[tone] || signoffs.Professional);

  return `${cta ? `\n\n${cta}` : ""}\n\n${signoff}\n[Your Name]`;
};

const generateSmartEmail = (data: EmailState, mode: string) => {
  const subject = generateSubject(
    data.subject,
    mode,
    data.options.addUrgency,
    data.tone
  );
  const open = opener(
    data.recipient,
    data.tone,
    data.options.addWarmth,
    data.options.humanize,
    mode
  );
  const mid = body(data.keyPoints, mode, data.options);
  const end = closing(mode, data.tone, data.options);

  return `${subject}\n\n${open}\n\n${mid}${end}`;
};

/* ============================
   UTIL
============================ */

const extractSubjectAndBody = (content: string) => {
  const m = content.match(/Subject:\s*(.*)/);
  return {
    subject: m ? m[1] : "",
    body: content.replace(/Subject:.*\n?/, "").trim()
  };
};

/* ============================
   MAIN COMPONENT
============================ */

export default function EmailAssistantPage() {
  const [formData, setFormData] = useState<EmailState>({
    recipient: "",
    subject: "",
    keyPoints: "",
    tone: "Professional",
    style: "Direct",
    length: "Medium",
    options: {
      includeCTA: false,
      addUrgency: false,
      humanize: true,
      formal: false,
      simplify: false,
      addWarmth: false
    }
  });

  const [versions, setVersions] = useState<DraftVersion[]>([]);
  const [activeVersionIdx, setActiveVersionIdx] = useState(-1);
  const [copied, setCopied] = useState(false);

  const handleGenerate = () => {
    const content = generateSmartEmail(formData, "Original");
    const v: DraftVersion = {
      id: crypto.randomUUID(),
      content,
      timestamp: new Date(),
      label: `Draft ${versions.length + 1}`,
      confidence: 94,
      type: "Original"
    };
    setVersions([...versions, v]);
    setActiveVersionIdx(versions.length);
  };

  const handleRewrite = (mode: string) => {
    const content = generateSmartEmail(formData, mode);
    const v: DraftVersion = {
      id: crypto.randomUUID(),
      content,
      timestamp: new Date(),
      label: mode,
      confidence: 98,
      type: "Rewrite"
    };
    setVersions([...versions, v]);
    setActiveVersionIdx(versions.length);
  };

  const copyText = (t: string) => {
    navigator.clipboard.writeText(t);
    setCopied(true);
  };

  /* UI BELOW ‚Äî UNCHANGED */
  /* ... existing JSX remains identical ... */

  return null;
}
